15 0v8.417a7.5 7.5 0 007.5 7.5zM17.99 0a7.5 7.5 0 00-7.5 7.5v8.417a7.5 7.5 0 0015 0V7.5a7.5 7.5 0 00-7.5-7.5z"/></g></svg>
